import logo from './logo.svg';
import './App.css';
import { useState } from 'react';
import { checkAllAchievements } from './checker'
import ReactDOM from 'react-dom/client';
import axios from "axios";

function App() {
  const [link, setLink] = useState("");
  const [name, setName] = useState("");
  const [achievements, setAchievements] = useState([]);

  const handleSubmit = (event) => {
    event.preventDefault();
    checkAllAchievements(link+".json", name).then(response => setAchievements(response));
    var str = '';
    achievements.forEach(function(slide) {
      str += '<div>'+ slide + '</div>';
    });
    document.getElementById("achievementContainer").innerHTML = str;
  }

  return (
    <div className="App">
      <header className="App-header">
        <form onSubmit={handleSubmit}>
          <center>
            <h1>Enter your replay!</h1>
            <div>
              <input 
                  type="text" 
                  value={link}
                  size={60}
                  onChange={(e) => setLink(e.target.value)}
                />
            </div>
            <br></br>
            <div>Enter your username!</div>
            (optional)
            <div>
              <input 
                type="text" 
                value={name}
                onChange={(e2) => setName(e2.target.value)}
              />
            </div>
            <br></br>
            Submit! (may take a bit to run)
            <br></br>
            <input type="submit" />
          </center>
        </form>
        <br></br>
        You achieved:
        <div id='achievementContainer'>

        </div>
      </header>
    </div>
  );
}

export default App;
