import { Pokedex } from './pokedex';
import { TypeChart } from './typechart';
import { Moves } from './moves';

// const test_replay = "https://replay.pokemonshowdown.com/gen9nationaldex-1854455622-oxyednpf3n3q85p9cd851ojj0ipjcb0pw"
// https://replay.pokemonshowdown.com/gen9nationaldex-1863462790 - LEVEL 90

const gymLeaders = {"eplsam" : "Ghost",
                    "eplwill" : "Bug",
                    "epljimmy" : "Fairy",
                    "eplribble" : "Ground",
                    "epljesse" : "Fire",
                    "eplbobby" : "Normal",
                    "epldanny": "Flying",
                    "eplyohan" : "Dragon",
                    "eplobserver": "Steel"};

export async function checkAllAchievements(replayLink, name) {
    //replayLink = "https://replay.pokemonshowdown.com/gen9nationaldex-1854455622-oxyednpf3n3q85p9cd851ojj0ipjcb0pw.json"
    //replayLink = "https://github.com/smogon/pokemon-showdown"
    if (!name) name = "";


    let response = await fetch(replayLink, { method:'GET' });
    let jsondata = await response.json();

    let player = determinePlayer(jsondata, name.toLowerCase().replace(/\s/g,''));

    // CALC ACHIEVEMENTS
    let achievements = [];
    achievements.push(monDown(jsondata, player));
    achievements.push(monOverboard(jsondata, player));
    achievements.push(sandbagging(jsondata, player));
    achievements.push(weakestLink(jsondata, player));
    achievements.push(theBestOffense(jsondata, player));

    achievements.push(alphabetic(jsondata, player));
    achievements.push(notFullyEvolved(jsondata, player));
    achievements.push(immovable(jsondata, player));
    achievements.push(mirrorMaster(jsondata, player));
    achievements.push(disrespect(jsondata, player));

    return achievements;
}

// HELPERS

function getIntersection(set1, set2) {
    const ans = new Set();
    for (let i of set2) {
        if (set1.has(i)) {
            ans.add(i);
        }
    }
    return ans;
}

function determinePlayer(json, name) {
    if (name != "") {
        if (json.p1id == name) return "p1";
        return "p2";
    }
    else {
        if (json.p1id in gymLeaders) return "p2";
        return "p1";
    }
}

function determineMonotype(json, player) {
    let types = null;

    let lines = json.log.split('\n');
    for (let line of lines) {
        if (line.substring(0,8) == "|poke|" + player) {
            let pokemon = line.substring(9).split(",")[0].toLowerCase().replace(/\W/g, '');
            if (types == null) types = new Set(Pokedex[pokemon]["types"])
            else types = getIntersection(types, new Set(Pokedex[pokemon]["types"]))
            if (types.size == 0) break;
        }
    }
    if (types.size == 0) return "None"; 
    return Array.from(types)[0];
}

function getBST(pokemon) {
    let sum = 0;
    for (let stat in Pokedex[pokemon]["baseStats"]) {
        sum += Pokedex[pokemon]["baseStats"][stat];
    }
    return sum;
}

// ACHIEVEMENTS

function monDown(json, player) {
    let numFainted = 0;

    let lines = json.log.split('\n');
    for (let line of lines) {
        if (line.substring(0,9) == "|faint|" + player) {
            numFainted++;
        }
    }
    if (numFainted > 1) return "";
    return "Mon Down"
}

function monOverboard(json, player) {
    let lines = json.log.split('\n');
    for (let line of lines) {
        if (line.substring(0,12) == "|teamsize|" + player) {
            let teamsize = Number(line[13]);
            if (teamsize < 6) return "Mon Overboard"
            return ""
        }
    }
    return ""
}

function sandbagging(json, player) {
    let lines = json.log.split('\n');
    for (let line of lines) {
        if (line.substring(0,8) == "|poke|" + player) {
            let pokeAttributes = line.substring(9).split(",");
            if (pokeAttributes.length == 1) return "";
            if (pokeAttributes[1].length > 1 && pokeAttributes[1][1] == "L") {
                if (Number(pokeAttributes[1].substring(2)) <= 90) continue;
            }
            return "";
        }
    }
    return "Sandbagging"
}

function weakestLink(json, player) {
    let lines = json.log.split('\n');
    for (let line of lines) {
        if (line.substring(0,8) == "|poke|" + player) {
            let pokemon = line.substring(9).split(",")[0].toLowerCase().replace(/\W/g, '');
            if (getBST(pokemon) <= 300) return "Weakest Link"
        }
    }
    return ""
}

function theBestOffense(json, player) {
    let lines = json.log.split('\n');
    for (let line of lines) {
        if (line.substring(0,8) == "|move|" + player) {
            let moveData = line.split("|");
            if (Moves[moveData[3].toLowerCase().replace(/\W/g, '')]["category"] == "Status") return ""
        }
    }
    return "The Best Offense"
}

function emptyHanded(json, player) {

}

function alphabetic(json, player) {
    let alpha = ""

    let lines = json.log.split('\n');
    for (let line of lines) {
        if (line.substring(0,8) == "|poke|" + player) {
            if (alpha == "") alpha = line[9];
            else {
                if (alpha != line[9]) return ""
            }
        }
    }
    return "Alphabetic"
}

function notFullyEvolved(json, player) {
    let lines = json.log.split('\n');
    for (let line of lines) {
        if (line.substring(0,8) == "|poke|" + player) {
            let pokemon = line.substring(9).split(",")[0].toLowerCase().replace(/\W/g, '');
            if (Pokedex[pokemon]["evos"] == null) return ""
        }
    }
    return "Not Fully Evolved"
}

function immovable(json, player) {
    let turnmode = false;
    let numSwitches = 0;

    let lines = json.log.split('\n');
    for (let line of lines) {
        if (line.substring(0,6) == "|turn|") {
            turnmode = true;
        }
        else if (line.substring(0,7) == "|upkeep") {
            turnmode = false;
        }
        else if (turnmode && (line.substring(0,10) == "|switch|" + player) && (line.split("|").length == 5)) {
            numSwitches++;
        }
    }
    
    if (numSwitches == 0) return "Immovable";
    return "";
}

function mirrorMaster(json, player) {
    let playerType = determineMonotype(json, player);
    let leader = ""

    if (player == "p1") leader = json.p2id
    else leader = json.p1id

    if (playerType == gymLeaders[leader]) return "Mirror Master"
    return "";
}

function disrespect(json, player) {
    let playerType = determineMonotype(json, player);

    if (playerType == "None") return ""

    let leader = ""

    if (player == "p1") leader = json.p2id
    else leader = json.p1id

    if (TypeChart[playerType.toLowerCase()]["damageTaken"][gymLeaders[leader]] == 1) return "Disrespect"
    return "";
}